.preview-section.general {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-auto-rows: auto;
  gap: 0.5rem;
  color: white;
  background-color: rgb(133, 175, 238);
}

.preview-section.general .name {
  grid-row: 1 / span 2;
  grid-column: 1;
}

.preview-section.general .title {
  grid-row: 3;
  grid-column: 1;
}

.preview-section.general .phone,
.preview-section.general .email,
.preview-section.general .address {
  grid-column: 2;
  text-align: end;
  font-size: 0.9rem;
}

.preview-section.general .phone {
  grid-row: 3;
}

.preview-section.general .description {
  grid-row: span 2;
  grid-column: 1 / span 2;
  width: 100%;
  word-wrap: break-word;
  line-break: normal;
  font-size: 0.8rem;
}

.preview-section {
  padding: 1rem;
}
.preview-section .title {
  color: #555;
}
/**Education**/

.preview-section.education {
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-rows: auto;
}
.preview-section.education .title {
  grid-column: span 2;
  grid-row: 1;
}

.school {
  display: grid;
  padding: 0.5rem 0;
  grid-template-columns: auto 1fr;
  column-gap: 1rem;
  justify-items: start;
  align-items: start;
}
.preview-section.education .dates {
  grid-column: 1;
  font-size: 0.9rem;
  grid-row: span 2;
}

.preview-section.education .school-name,
.preview-section.education .major {
  font-size: 1rem;
  grid-column: 2;
}

.preview-section.job-exp {
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-rows: auto;
}

.job {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-auto-rows: auto;
  gap: 0.5rem;
  align-items: flex-start;
  padding: 0.5rem 0;
}

.name,
.position,
.date {
  grid-column: 1;
}

.desc {
  grid-column: 2;
  grid-row: 1 / span 3;
}
